@import 'vars';

.Mui-disabled {
  background-color: #2C2F38 !important;
  border-radius: 4px 4px 0px 0px;
}
.MuiFilledInput-underline.Mui-disabled:before {
  border-bottom-style: solid !important;
}
textarea, input {
  color: rgba(255,255,255,0.6) !important
}
.MuiPaper-elevation1 {
  box-shadow: none !important
}
.MuiExpansionPanel-root:before {
  display: none;
}
.MuiExpansionPanelSummary-content {
  margin: 20px 0 !important;
  flex-direction: row;
  justify-content: space-between;
  align-items:center;
}
.MuiExpansionPanel-root.Mui-expanded {
  margin: 0 !important;
}

.MuiTableRow-root.Mui-selected, .MuiTableRow-root.Mui-selected:hover {
  background-color: rgba(220, 0, 78, 0.08);
}
.MuiSwitch-colorSecondary.Mui-checked:hover {
  background: #A1AFDB;
}

.MuiSwitch-colorSecondary.Mui-checked + .MuiSwitch-track {
  background-color: #A1AFDB !important;
}
.Mui-checked {
  .MuiSwitch-thumb {
    background-color:#A1AFDB !important;
  }
}
.MuiSwitch-thumb {
  background-color:rgba(255, 255, 255, 0.6) !important;
}

.MuiSwitch-track {
  background-color: rgba(255, 255, 255, 0.6) !important;
}

.MuiSlider-thumb {
  background: #A1AFDB !important;
}
.MuiSlider-root {
  span {
    background: #A1AFDB !important;
  }
}
.MuiFormControl-root{
  label {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.6);
    letter-spacing: 0.4px;
  }
  label.Mui-focused {
    color: #A1AFDB;
  }
  input, .MuiSelect-select {
    color: rgba(255, 255, 255, 0.86) !important;
  }
}
.MuiInputLabel-filled.MuiFormLabel-filled, .MuiInputLabel-filled.MuiInputLabel-shrink {
  transform: translate(12px, 13px) scale(0.75) !important;
}
.MuiFilledInput-root {
  background-color: #434753 !important;
  transition-duration:.3s;
  &:hover {
    background-color: #3e4350 !important;
    transition-duration:.3s;
  }
}
.MuiInputBase-root.Mui-focused {
   background-color: #3e4350 !important;
 }




.inputDollar {
  .MuiInputBase-root {
    padding-left: 10px;
    background: url('./../Images/dollar.svg') 12px 28px no-repeat;
  }
}
.MuiFilledInput-underline:before {
  border-bottom: 1px solid rgba(255, 255, 255, 0.36) !important;
}



table thead .MuiTableCell-root {
padding-top: 17px;
  padding-bottom: 17px;
}

table .MuiTableCell-root {
  border-bottom:none;
  font-family: 'Open Sans', sans-serif !important;
  span {
    display: inline-flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items:center;
  }
}
body .MuiFormControl-root *{
  font-family: 'Open Sans', sans-serif !important;
}

.MuiFormControl-root .Mui-disabled {
  color: rgba(255,255,255,0.38) !important;
  background-color: #383c45 !important;
}




.MuiSwitch-root {
  width: 49px !important;
  height: 36px !important;

  .MuiSwitch-switchBase {
    padding: 7px;
  }
  .MuiSwitch-thumb {
    width: 17px;
    height: 17px;
    margin-top: 2px;
  }
  .MuiSwitch-thumb {
    background-color: #a7a7a7 !important;
  }
  .Mui-checked .MuiSwitch-thumb {
    background-color: #A1AFDB !important;
  }
  .MuiSwitch-switchBase.Mui-checked {
    transform: translateX(19px) !important;
  }

}


.MuiExpansionPanelSummary-root.Mui-expanded {
  height: 55px;
}


.MuiMenuItem-root {
  font-family: 'Open Sans', sans-serif !important;
}

.MuiSlider-thumb.Mui-focusVisible, .MuiSlider-thumb:hover {
  box-shadow: 0px 0px 0px 9px rgba(255, 255, 255, 0.05) !important;
}
.MuiSlider-thumb.MuiSlider-active {
  box-shadow: 0px 0px 0px 14px rgba(255, 255, 255, 0.05) !important;
}


.MuiButton-contained {
  box-shadow: none !important;
  &:hover {
    box-shadow: 0px 10px 10px -4px rgba(0,0,0,0.2) !important;
  }
}
.MuiIconButton-colorPrimary:hover {
  background-color: rgba(255, 255, 255, 0.05) !important;
}

.MuiButton-label {
  line-height: 1 !important;
}

.MuiFormControl-root.color-red input {
  color: $red !important;
}
.MuiFormControl-root.color-green input {
  color: $green !important;
}


.filterPanel input {
  transition-duration: .3s;
  &:hover {
    background-color: #3e4350 !important;
    transition-duration: .3s;
  }
}