.preloader__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items:center;
  height: 500px;
  &--fullscreen {
    height: 100vh;
  }
}
.preloader--circle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items:center;
   .line {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    background-color:#A1AFDB;
     margin: 0 2px;
  }
  .line:nth-last-child(1) {
    animation: loadingC 0.45s 0.1s linear infinite;
  }
  .line:nth-last-child(2) {
    animation: loadingC 0.45s 0.2s linear infinite;
  }
  .line:nth-last-child(3) {
    animation: loadingC 0.45s 0.3s linear infinite;
  }
}


@keyframes loadingC {
0 {
  transform: translate(0, 0);
}
50% {
    transform: translate(0, 15px);
  }
100% {
     transform: translate(0, 0);
   }
}